.formulario{
    display: flex;
    flex-wrap: wrap;    
    width: 100%;
}
.ingresarCitas{
    width: 40%;
}
.form-field{
    display: flex;
    align-items: center;
    padding: 1em 0 ;
    width: 100%;
}
.formulario label{
    width: 30%;
    font-size: 1.2em;
    padding: 0.8em;
    font-weight: bold;
    display: inline;
}
.formulario input,textarea{
    display: inline;
    border-radius: 6px;
    padding: 0.5em;
    height: 2em;
    width: 60%;
    border: none;
}
.formulario textarea{
    height: 5em;
}
.botones-form{
    display: flex;
    justify-content: space-around;
    width: 100%;
  }

  @media (max-width: 750px) {
    .contenido{
        min-height: 100vh;
        height: 100%;
        padding-top: 20%;
    }
    .formulario{
        padding-bottom: 2em;
    }
    .ingresarCitas{
        width: 90%;
        min-height: 100%;
        height: 100%;
    }
    .form-field{
        flex-direction: column;
        align-items: center;
        width: 100%;
    }
    .formulario label{
        width: 100%;
        text-align: center;
    }
    .formulario input,textarea{
        padding: 0.5em;
        height: 2em;
        width: 80%;
       
    }
    .button{
        height: 100px;
    }
    
  }