.navbar{
    width: 100vh;
    background-color: blue;
    position: fixed;
    top: 0;
    color:black;
    height: 65px;
    background-color: rgba(63, 68, 65, 0.4);
    z-index: 100;
}

@media (max-width: 750px) {
    .navbar{
        
        overflow: hidden;
        display: grid;
        grid-template-areas: "brand title burguer";
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr;
        right: 0;
        left: 0;
        top: 0;
        height: auto;
        width: 100%;
                 
    }

    .navbar-title{
        grid-area: title;
        text-align: center;
        font-size: 1em;
    }
    .navbar .brand{
       grid-area: brand;
       display: flex;
       justify-content: flex-start;
       align-items: center;
       cursor: pointer;

    }
    .brand img{
        width: 70px;
        height: 70px;
        margin-left: 2em;
        cursor: pointer;

    }
    .navbar .navbar-title h1{
        font-size: 1.3em;
    }
    .navbar .burguer-button{
        height: 100%;
        grid-area: burguer;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        
        
    }
    .navbar .burguer-button button{
        margin-right: 2em;
        width: 40px;
        height: 40px;
        background: none;
        border: solid white 1px;
        color: white;
        cursor: pointer;
        transition: 0.5s;
        z-index: 200;
    }
    .turnBurguer{
        transform: rotate(90deg);
        transition: 0.5s;

    }
   
}