.SideMenu{


    
    position: fixed;
    top: 0;
    background-color: rgba(63, 68, 65,0.9);
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    left: 100%;

 
}
.SideMenu button{

    padding: 1em;
    margin: 1em 1em;
    background: none;
    border: solid 2px white;
    font-weight: bolder;
    color: white;
    font-size: 1.3em;
    border-radius: 2px;
    transition: 1s;
    
}
.hideSideMenu{
  animation-duration: 0.5s;
  animation-name: slidein;
  animation-fill-mode: forwards;
}

@keyframes slidein {
    from {
        left: 100%;
    }
  
    to {
        left: 0%;
    }
  }
