

@media (max-width: 750px) {
.tarjeta{
    border: solid white 1px;
    width: 90%;
    text-align: center;
    font-size: 1.2em;
    margin: 0.5em 0;
    padding: 1em;
    box-sizing: border-box;
    border-radius: 6px;
    background-color: rgba(94, 111, 100,0.5)
}
.img-tarjeta{
    max-width: 100%;
    width: 150px;
    height: 150px;
    border-radius: 50%;
}
}